.modal_modal__2bhYs{bottom:0;left:0;position:absolute;right:0;top:0;z-index:100}.modal_modal__2bhYs:after{background:#000;bottom:0;content:' ';left:0;opacity:.5;position:absolute;right:0;top:0}.modal_modal__2bhYs>div{background:#fff;box-shadow:0 1px 4px rgba(0,0,0,0.3),0 0 40px rgba(0,0,0,0.1) inset;box-sizing:border-box;border-radius:5px;display:flex;flex-direction:column;height:65%;margin:auto;padding:0;position:relative;top:50%;transform:translateY(-50%);width:65%;z-index:1000}.modal_modal__2bhYs>div>header{background:#ccc;border-bottom:0 none;border-top-left-radius:5px;border-top-right-radius:5px;box-shadow:0 1px 5px rgba(0,0,0,0.46);display:flex;font-size:1em;font-weight:bold;height:1.5em;line-height:1.5em;padding:.25em 1em}.modal_modal__2bhYs>div>header span{color:#000;flex:1 1}.modal_modal__2bhYs>div>header button{background:inherit;border:0;cursor:pointer;order:1;text-align:center}.modal_modal__2bhYs>div>header button:hover{background:#000;color:#fff}.modal_modal__2bhYs>div>div{flex:1 1;overflow:auto;padding:25px}

body,html{height:100%;margin:0;padding:0;width:100%;background-color:#eee;font-family:sans-serif}.todo-details{display:flex;flex-direction:column}.todo-details header{display:flex;flex-direction:row;justify-content:space-between;padding:.5em 1em}.todo-details .item{flex:1 1;padding:1em}.todo{display:flex;flex-direction:row;justify-content:flex-start;margin:4em}.todo form{display:flex;flex-direction:column;margin-right:2em}.todo form label{display:flex;flex-direction:column;margin-bottom:1em}.todo ul{list-style-type:none;margin:0;padding:0}.todo ul li{align-items:center;display:flex;flex-direction:row;font-size:.75em;margin:.75em .5em}.todo ul li.complete-true span{background:pink;text-decoration:line-through}.todo ul li.complete-true span:hover{background:seagreen}.todo ul li span{background:#e0e0e0;cursor:pointer}.todo ul li span:hover{background:pink}.todo ul li span,.todo ul li input{border:.5px solid #eee;display:inline-block;padding:.2em .25em;width:300px}.todo ul li button{background:transparent;border:none;color:blue;cursor:pointer;padding:0;margin:0 .75em}footer{text-align:center}.tasks{font-size:25px;font-family:cursive}header h2{margin:30px;font-family:'Fredoka One', cursive;font-size:30px}

